import {
  Component,
  Input,
  ContentChildren,
  QueryList,
  forwardRef,
  AfterContentInit,
} from '@angular/core';
import {SBItemComponent} from './sb-item';
import {sbConfig, sbItems} from './sb.config';

@Component({
  exportAs: 'squeezebox',
  selector: 'mdb-squeezebox, mdb-accordion',
  templateUrl: 'squeezebox.html'
})
export class SqueezeBoxComponent implements AfterContentInit {

  @Input() multiple = true;

  @ContentChildren(forwardRef(() => SBItemComponent)) items: QueryList<SBItemComponent>;

  constructor() {
    sbConfig.serviceInstance = this;
  }

  didItemToggled(item: SBItemComponent) {
    // on not multiple, it will collpase the rest of items
    if (!this.multiple) {
      sbItems.forEach((el: any) => {
        if (el !== item) {
          el.applyToggle(true);
        }
        if (el === item) {
          const collapsed = el.collapsed ? true : false;
          setTimeout(() => {
            el.applyToggle(collapsed);
          }, 0);
        }
      });
    }
  }

  ngAfterContentInit(): void {
    this.items.forEach((el: any) => {
        sbItems.push(el);
      }
    );

    if (!this.multiple) {
      sbItems.forEach((el: any) => {
        const collapsed = el.collapsed ?  true : false;
        el.applyToggle(collapsed);
      });
    }
  };


}
